import { ImageBackground, StatusBar, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { wellCome_background } from '../../assets/images';
import LinearGradient from 'react-native-linear-gradient';
import colors from '../../theme/colors';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { SCREENS_ENUM } from '../../types/global';
import {
  NavigationContainerRefWithCurrent,
  StackActions,
} from '@react-navigation/native';
import { STORAGE_KEY, StorageService } from '../../services/StorageService';
import dayjs from 'dayjs';
import { useCallback, useEffect } from 'react';
import { logger } from '../../utils';
import { useDispatch, useSelector } from 'react-redux';
import {
  hideSelectedLanguageModal,
  hideVideoModal,
  setFirstScreenOpenApp,
  showSelectedLanguageModal,
  showVideoModal,
} from '../../redux/features/app/actions';
import { HomeMedia } from '../../redux/features/app/types';

interface WellComeProps {
  navigationRef: NavigationContainerRefWithCurrent<any>;
}

const WellComeScreen = ({ navigationRef }: WellComeProps) => {
  const dispatch = useDispatch();

  const { t } = useAppTranslation();
  const inset = useSafeAreaInsets();
  const dataMedia: { [key: string]: HomeMedia } | null = useSelector(
    (state: any) => state.app.dataMedia,
  );

  const checkOpenFirstVideo = useCallback(
    async (lang?: string) => {
      try {
        const isOpenFirstVideo = await StorageService.get(
          STORAGE_KEY.IS_FIRST_TIME_OPEN_APP,
        );
        if (isOpenFirstVideo) {
          return;
        }

        dispatch(
          showVideoModal({
            initialSubtitle: lang || 'en',
            visible: true,
            source: dataMedia?.['1_INTRO_VIDEO']?.url || {},
            subtitle: dataMedia?.['1_INTRO_VIDEO']?.subtitle || {},
            onClose: () => {
              dispatch(hideVideoModal());
            },
          }),
        );
      } catch (error) {
        logger('checkOpenFirstVideo', error);
      }
    },
    [dispatch, dataMedia],
  );

  useEffect(() => {
    dispatch(
      showSelectedLanguageModal({
        visible: true,
        onClose: (lang?: string) => {
          setTimeout(() => {
            checkOpenFirstVideo(lang);
          }, 350);
          dispatch(hideSelectedLanguageModal());
        },
      }),
    );
  }, [dispatch, checkOpenFirstVideo]);

  return (
    <ImageBackground source={wellCome_background} style={styles.container}>
      <StatusBar barStyle="light-content" translucent />
      <LinearGradient
        colors={['transparent', colors.black]}
        locations={[0, 0.7]}
        style={styles.container}
      >
        <View style={styles.contentContainer}>
          <Text style={styles.title}>{t('WELLCOME_TO_TRADE_WIZE')}</Text>
        </View>

        <Button
          size="large"
          title={t('GET_STARTED')}
          style={[styles.button, { marginBottom: inset.bottom + 16 }]}
          onPress={() => {
            navigationRef.dispatch(
              StackActions.replace(SCREENS_ENUM.AuthNavigator),
            );
            dispatch(setFirstScreenOpenApp(SCREENS_ENUM.AuthNavigator));
            StorageService.set(
              STORAGE_KEY.IS_FIRST_TIME_OPEN_APP,
              dayjs().format('YYYY-MM-DD HH:mm:ss'),
            );
          }}
        />
      </LinearGradient>
    </ImageBackground>
  );
};

export default WellComeScreen;
